export default {
	data() {
		return {
			  // 判断黑色的幕布是否显示
			judgemb : false,
			  // 判断分享部分是否显示
			judgeshare : false,
			name : ""
		}
	},
	onLoad(options) {
	    this.name = options.name;
	},
	methods: {
		back : function(){
		   uni.navigateBack({
		
		   });
		},
		
		// 点击邀请好友
	 yqhy : function(){
		 this.judgemb = true;
		 this.judgeshare = true;	 
	 },
	   // 点击隐藏分享部分
	 close : function(){
		 this.judgemb = false;
		 this.judgeshare = false;	 
	 },
	 
	    // 去我的团队页面
	 wdtd : function(){
		 uni.navigateTo({
		 	url : "team/team"
		 })
	 },
	 
	
		 
		 
		 // 分享给朋友
		 sharehy : function(){
			 uni.share({
			     provider: "weixin",
			     scene: "WXSceneSession",
			     type: 0,
			     title: "来和我一起赚钱把",
			     imageUrl: "http://temp.anzi123.top/dhzsimg/sar.jpg",
			     success: function (res) {
			         console.log("success:" + JSON.stringify(res));
			     },
			     fail: function (err) {
			         console.log("fail:" + JSON.stringify(err));
			     }
			 });
		 },
		 
		 
	 sharepyq : function(){
		 // 分享到朋友圈
		uni.share({
		    provider: "weixin",
		    scene: "WXSenceTimeline",
		    type: 2,
		    imageUrl: "http://temp.anzi123.top/dhzsimg/sar.jpg",
		    success: function (res) {
		        console.log(res);
		    },
		    fail: function (err) {
		        console.log(err);
		    }
		});
	 },
	 
	 // 保存图片到本地
	 saveimg : function(){
		 uni.showLoading({
		 	title: '图片正在下载',
		 	mask : true
		 });
		 
		 uni.downloadFile({
		 	url : "http://temp.anzi123.top/dhzsimg/sar.jpg",
		 	success:function(result){
		 		   // 下载完图片后的临时路径
		 		var tempFilePath = result.tempFilePath;
		 		uni.hideLoading();
		 		uni.saveImageToPhotosAlbum({
		 			filePath:tempFilePath,
		 			success : function(){
		 				uni.showToast({
		 					title:'图片保存成功,请到您的相册里查看',
							icon : "none"
		 				});
		 			}
		 		})
		 	}
		 });
		 
		
	 }
	 
	    
	 
	 
	}
}
